.col-xs-4 {
    position: relative;
}

img {
  opacity: 1;
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.middle {
 -webkit-transition: .5s ease;
 -o-transition: .5s ease;
 transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 91%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%)
}

.col-xs-4:hover .image {
  opacity: 0.8;
}

.col-xs-4:hover .middle {
  opacity: 1;
}

.text {
  background-color: #4CAF50;
  color: white;
  font-size: 16px;
  padding: 16px 25px;
}


#video{
  position: relative;
    /*left: -80px;*/
}

#rid-background{
   background-color: transparent;
}



